import React from "react";
import './TopNav.css';
import importAllImagesText from '../Helpers/ImportAllImagesText';

const logo = await importAllImagesText(require.context('../PartnersLogos/SiteLogo', false, /\.(webp)$/));

export default function TopNav() {

    return (
        <div className="top-navbar">
            <div className="nav-temple-logo-container">
                <img src={logo[0].image} alt="Temple Logo" />
            </div>
            <div>
                <h1>OUR PARTNERS</h1>
            </div>
        </div>
    );
}
