export default async function importAllImagesText(r) {
    let images = [];
    r.keys().map((item) => { 
        const fileName = item.split('/').pop(); // Estrae solo il nome del file dal percorso
        const name = fileName.substring(0, fileName.lastIndexOf('.')); // Rimuove l'estensione dal nome del file
        images.push({ name: name, image: r(item) });
    });
    return images;
}