import React, { useState, useEffect } from 'react';
import BottomNavigation from '@mui/material/BottomNavigation';
import BottomNavigationAction from '@mui/material/BottomNavigationAction';
import HomeIcon from '@mui/icons-material/Home';
import RestaurantMenuOutlinedIcon from '@mui/icons-material/RestaurantMenuOutlined';
import HotelOutlinedIcon from '@mui/icons-material/HotelOutlined';
import DiamondOutlinedIcon from '@mui/icons-material/DiamondOutlined';
import LocalBarOutlinedIcon from '@mui/icons-material/LocalBarOutlined';
import { useNavigate } from 'react-router-dom';
import './BottomNav.css';
import styled from 'styled-components';

const StyledBottomNav = styled(BottomNavigation)`
  &&{
  max-width: 100vw !important;
  width: 100% !important;
  position: fixed !important;
  bottom: 0 !important;
  transition: all 0.3s ease !important;
  backdrop-filter: blur(8.7px);
  -webkit-backdrop-filter: blur(8.7px);
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
  box-shadow: 0 4px 30px rgb(255 255 255 / 60%);

  }
  @media (prefers-color-scheme: light) {
    && {
     background: rgba(255, 255, 255, 0.73);
    }
  }

  @media (prefers-color-scheme: dark) {
    && {
      background: rgba(0, 0, 0, 0.73);
    }
  }
`;
const StyledButtonNav = styled(BottomNavigationAction)`
  @media (prefers-color-scheme: light) {
  && {
    color: black !important; 
  }
  .Mui-selected {
    color: black !important;
    font-weight: bolder;
  }
  }

  @media (prefers-color-scheme: dark) {
  && {
    color: white !important;
  }
  .Mui-selected {
    color: white !important;
    font-weight: bolder;
  }
  }
`;

export default function LabelBottomNavigation() {
  const [value, setValue] = useState('/');
  const [prevScrollPos, setPrevScrollPos] = useState(0);
  const [visible, setVisible] = useState(true);
  const nav = useNavigate();

  const handleScroll = () => {
    const currentScrollPos = window.pageYOffset;

    setVisible(prevScrollPos > currentScrollPos || currentScrollPos < 10);

    setPrevScrollPos(currentScrollPos);
  };

  useEffect(() => {
    window.addEventListener('scroll', handleScroll);

    return () => {
      window.removeEventListener('scroll', handleScroll);
    };
  }, [prevScrollPos, visible]);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  useEffect(() => {
    nav(value);
  }, [value]);



  return (
    <StyledBottomNav className={`bottom-nav ${visible ? 'visible' : 'hidden'}`} value={value} onChange={handleChange}>
      <StyledButtonNav
        label="Restaurants"
        value="restaurants"
        icon={<RestaurantMenuOutlinedIcon />}
      />
      <StyledButtonNav
        label="Hotels"
        value="hotels"
        icon={<HotelOutlinedIcon />}
      />
      <StyledButtonNav
        label="Home"
        value="/"
        icon={<HomeIcon />}
      />
      <StyledButtonNav
        label="Locals"
        value="locals"
        icon={<DiamondOutlinedIcon />}
      />
      <StyledButtonNav
        label="Bars"
        value="bars"
        icon={<LocalBarOutlinedIcon />}
      />
    </StyledBottomNav>
  );
}