import React, { lazy } from "react";
// import Home from "./Components/Home/Home";
// import Restaurants from "./Components/Restaurants/Restaurants";
// import Hotels from "./Components/Hotels/Hotels";
// import Locals from "./Components/Locals/Locals";
// import Bars from "./Components/Bars/Bars";
// import NotFound from "./Components/NotFound/NotFound";
import Loader from "./Components/Helpers/Loader";
import TopNav from "./Components/TopNav/TopNav";
import LabelBottomNavigation from "./Components/Navbar/BottomNav";
import { HashRouter as Router, Route, Routes, useLocation } from 'react-router-dom';

const lazyWrapper = (importStatement) => {
  const Component = lazy(importStatement);
  return (props) => (
    <React.Suspense fallback={<Loader />}>
      <Component {...props} />
    </React.Suspense>
  );
};


function App() {
  return (
    <Router>
      <AppRoutes />
    </Router>
  );
}

const Home = lazyWrapper(() => import("./Components/Home/Home"));
const Restaurants = lazyWrapper(() => import("./Components/Restaurants/Restaurants"));
const Hotels = lazyWrapper(() => import("./Components/Hotels/Hotels"));
const Locals = lazyWrapper(() => import("./Components/Locals/Locals"));
const Bars = lazyWrapper(() => import("./Components/Bars/Bars"));
const NotFound = lazyWrapper(() => import("./Components/NotFound/NotFound"));
const GoToTop = lazyWrapper(() => import("./Components/Helpers/GoToTop"));


function AppRoutes() {
  const permittedLocations = ['/', '/restaurants', '/hotels', '/locals', '/bars'];
  const location = useLocation();

  const isNotFoundRoute = !permittedLocations.includes(location.pathname);

  return (
    <>
      {!isNotFoundRoute && <TopNav />}
      <Routes>
        <Route path="/" element={<Home />} />
        <Route path="/restaurants" element={<><div className="container-page-content"><Restaurants /></div></>} />
        <Route path="/hotels" element={<><div className="container-page-content"><Hotels /></div></>} />
        <Route path="/locals" element={<><div className="container-page-content"><Locals /></div></>} />
        <Route path="/bars" element={<><div className="container-page-content"><Bars /></div></>} />
        {isNotFoundRoute && <Route path="*" element={<NotFound />} />}
      </Routes>
      {!isNotFoundRoute && <GoToTop />}
      {!isNotFoundRoute && <LabelBottomNavigation />}
      {/* {!isNotFoundRoute && <CookieBanner show={localStorage.getItem('michaelsCookiesAccepted') || sessionStorage.getItem('michaelsCookiesRefused') ? false : true} />} */}
    </>
  );
}
export default App;
